import { React , Component } from "react";
import Header from './headerComponent';
import Menu from './menuComponent';
import { Switch, Route, Redirect, withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import {loginUser,fetchImages,logoutUser,imageUpload} from '../redux/ActionCreators';
const mapStateToProps = state => {
    return {
      auth: state.auth,
      images:state.images
    }
}
const mapDispatchToProps = (dispatch) => ({
    loginUser: (creds) => dispatch(loginUser(creds)),
    fetchImages:()=>{dispatch(fetchImages())},
    logoutUser: () => {dispatch(logoutUser())},
    imageUpload :(imageToUpload) => dispatch(imageUpload(imageToUpload))
    
});
class Main extends Component{
    componentDidMount(){
        this.props.fetchImages();
    }
    render(){

        
        return(
            <>
            <Header auth={this.props.auth} 
                    loginUser={this.props.loginUser}
                    logoutUser={this.props.logoutUser}
            />
            <Switch>
                <Route path="/home" component={()=> <Menu images={this.props.images} auth={this.props.auth} imageUpload={this.props.imageUpload} />} />
                <Redirect to="/home" />
            </Switch>
            
            </>
        );
    }
}
export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Main));