import { React , Component } from "react";
import { Navbar, NavbarBrand, Nav, NavbarToggler, Collapse, NavItem, Jumbotron,
    Button, Modal, ModalHeader, ModalBody,
    Form, FormGroup, Input, Label } from 'reactstrap';
import isLoading from './isLoadingComponent';
class Menu extends Component{
    constructor(props){
        super(props);
        this.handleupload = this.handleupload.bind(this);
    }
    handleupload(e){
        this.props.imageUpload(this.image.files[0]);
        e.preventDefault();
    }
    render(){
        const images = this.props.images.images.map(image=>{
            return(
                <div class="col">
                    <div class="card">
                        
                        <div class="card-body">
                            <img src={'http://localhost:9000/'+image.image} class="card-img-top" />
                            <p class="card-text">{image.user.username}</p>
                        </div>
                    </div>
                </div>
            )
        })
        if(this.props.images.isLoading){
            return(
                <isLoading />
            );
        }
        else if(this.props.images.errMess){
            return(
            <h1>{this.props.images.errMess}</h1>
            )
        }
        else{
            return(
                <>
                
                    <div class="container">
                        {this.props.auth.isAuthenticated?
                            <div class ="row">
                                <Form onSubmit={this.handleupload} className="justify-content-center">
                                    <FormGroup className="input-group mb-3">
                                        <Label htmlFor="username" className="input-group-text" for="inputGroupFile01">Username</Label>
                                        <Input type="file" id="image inputGroupFile01" name="image"
                                            className="form-control" innerRef={(input) => this.image = input} />
                                    </FormGroup>
                                    <Button type="submit" value="submit" color="primary">Login</Button>
                                </Form>
                            </div>
                            :
                            <></>
                        }
                        <div class="row row-cols-1 row-cols-md-2 g-4">
                            {images}
                        </div>
                    </div>
                </>
            );
        }
    }
}
export default Menu;