import * as ActionTypes from './ActionTypes';




export const requestLogin = (creds) => {
    return {
        type: ActionTypes.LOGIN_REQUEST,
        creds
    }
}
  
export const receiveLogin = (response) => {
    return {
        type: ActionTypes.LOGIN_SUCCESS,
        token: response.token
    }
}
  
export const loginError = (message) => {
    return {
        type: ActionTypes.LOGIN_FAILURE,
        message
    }
}

export const loginUser = (creds) => (dispatch) => {
    // We dispatch requestLogin to kickoff the call to the API
    dispatch(requestLogin(creds))

    return fetch('http://localhost:9000/users/login', {
        method: 'POST',
        headers: { 
            'Content-Type':'application/json'
        },
        body: JSON.stringify(creds)
    })
    .then(response => {
        if (response.ok) {
            return response;
        } else {
            var error = new Error('Error ' + response.status + ': ' + response.statusText);
            error.response = response;
            throw error;
        }
        },
        error => {
            throw error;
        })
    .then(response => response.json())
    .then(response => {
        if (response.success) {
            
            localStorage.setItem('token', response.token);
            localStorage.setItem('creds', JSON.stringify(creds));
            
            
            dispatch(receiveLogin(response));
        }
        else {
            var error = new Error('Error ' + response.status);
            error.response = response;
            throw error;
        }
    })
    .catch(error => dispatch(loginError(error.message)))
};
export const requestLogout = () => {
    return {
      type: ActionTypes.LOGOUT_REQUEST
    }
}
  
export const receiveLogout = () => {
    return {
      type: ActionTypes.LOGOUT_SUCCESS
    }
}

// Logs the user out
export const logoutUser = () => (dispatch) => {
    dispatch(requestLogout())
    localStorage.removeItem('token');
    localStorage.removeItem('creds');
    dispatch(receiveLogout())
}
export const fetchImages = () => (dispatch) =>{
    dispatch(imagesloading());
    return fetch('http://localhost:9000/images/')
    .then(response => {
        if (response.ok) {
            return response;
        }
        else {
            var error = new Error('Error ' + response.status + ': ' + response.statusText);
            error.response = response;
            throw error;
        }
    },
    error => {
        var errmess = new Error(error.message);
        throw errmess;
    })
    .then(response => response.json())
    .then(images => dispatch(addImages(images)))
    .catch(error => console.log(error));  
};
export const imagesloading=()=>{
    return {
        type:ActionTypes.IMAGES_LOADING
    }
}
export const addImages =(images)=>({
    type:ActionTypes.ADD_IMAGES,
    payload:images
});
export const imageError = (errMess)=>({
    type:ActionTypes.IMAGE_ERROR,
    payload:errMess
})

export const postImage=(image)=>(dispatch) =>{
    const bearer = 'Bearer ' + localStorage.getItem('token');
    return fetch('http://localhost:9000/images/',{
        method:"POST",
        headers: { 
            'Content-Type':'application/json',
            'Authorization': bearer
        },
        body: JSON.stringify(image)
    })
    .then(response => {
        if (response.ok) {
            return response;
        } else {
            var error = new Error('Error ' + response.status + ': ' + response.statusText);
            error.response = response;
            throw error;
        }
        },
        error => {
            throw error;
        })
    .then(response => response.json())
    .then(response=>dispatch(fetchImages()))
    .catch(error => dispatch(imageError(error.message)));  
};
export const imageUpload = (imageToUpload) =>(dispatch)=>{
    const fd = new FormData();
    fd.append('imageFile',imageToUpload);
    return fetch('http://localhost:9000/imageupload',{
        method:"POST",
        body:fd
    })
    .then(response=>response.json())
    .then(response => dispatch(postImage(response)))
    .catch(error => dispatch(imageError(error.message))); 
}







