import * as ActionTypes from './ActionTypes';

export const Images = (state={
    images:[],
    isLoading:false,
    errMess:null
},Action) =>{
    switch(Action.type){
        case ActionTypes.IMAGES_LOADING:
            return{...state,images:[],isLoading:true,errMess:null}
        case ActionTypes.IMAGE_ERROR:
            return{...state,images:[],isLoading:false,errMess:Action.payload}
        case ActionTypes.ADD_IMAGES :
            return {...state,images:Action.payload,isLoading:false,errMess:null};
        default:
            return state;
    }
}